:root{

    --bgCard : #F4F4F4;
    --textCard : #212121

    }
    

#nav {
    background-color: var(--bgCard);
    color: var(--textCard);
    position: fixed;
    height: 100vh;
    width: 290px;
    top: 0;
    z-index: 50;
    box-shadow: 1px -2px 4px 1px #666;
    border-right:solid 1px #eee;
    transition:ease-in-out .5s ;
}

.navHidden{
    left: -100vw;
}

.navShow {
    left: 0vw;
}
#nav li {
    list-style: none;
    color: var(--textCard);
    
}

#nav li:hover {
    color: #26777A;
}

.ImportContactsIcon:hover {
    color: #26777A !important;
}
.titleNav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0px 0px 0 0px;
    padding: 0 10px;
    z-index: 999;
    position: absolute;
    top: 0;
    left: 0;right: 7px;
    background-color: #F4F4F4;
    box-shadow: 0px 10px 5px -9px rgba(74,74,74,0.15);
-webkit-box-shadow: 0px 10px 5px -9px rgba(74,74,74,0.15);
-moz-box-shadow: 0px 10px 5px -9px rgba(74,74,74,0.15);
   
}

.btnCloseNav{
    cursor: pointer;
    opacity: 0.5;


}

.navButtonShow{
    opacity: 0.8;
}

.navButtonShow:hover{
    opacity: 1;
}

.btnCloseNav:hover{
    opacity:1;
}
.listDetail { 
    margin:3rem 0 1rem 0 ;
    overflow: scroll;
    height: 100vh;
    

}
.listDetail li{
    padding: .7rem 1rem;
    cursor: pointer;
    background-color: #e9e9e9;
    color: #888;
}

.listDetail li:hover {
    background-color: inherit;
    transition: ease-in-out .2s;
}

#navHead{
    padding: 1rem 1.5rem;
    width: 100%;
    background-color: #F4F4F4;
    border-radius: 10px;
    box-shadow: 0px 5px 6px -7px;
}

@media screen and (max-width : 731px) and ( min-width:70px) {
    nav{
        width: 100vw;
    }
}