footer {
    margin: 1rem .5rem;
    background-color: #006064;
    color: #f5f5f5;
    border-radius: 5px;padding:2rem 1rem;
    box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.69);
-webkit-box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.69);
-moz-box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.69);
}

.footerMain{    display: flex;justify-content: space-around;
}

footer li {
    list-style: none;
}

footer ul {
    display: block;
    list-style-type: disc;
    margin-block-start: 0em;
    margin-block-end: 0em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 0px;
}

footer h5{
    margin-bottom: 1rem !important;

}

.terms span{
 opacity: .6;
}

.ListIconFooter {
    display: flex;
    gap: 1rem;
}

.ListIconFooter  .iconFooter{transform: scale(1.2);}