:root{

    --bgCard : #F4F4F4;
    --textCard : #212121

    }
    

   
#Card {
    background-color: var(--bgCard);
    color: var(--textCard);
    margin-top: 1rem;
}

.GridMenu{
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(250px,350px));gap:15px 15px;
    place-content: center;
    
}

audio{
    padding: 0px !important;
    margin: 0px !important;
    text-align: left;
    width: 280px;
}


#ErrorHandling{
    background-color: #eee; 
    /* backdrop-filter: blur(3px); */
    text-align: center;
    color:var(--textCard) ;
    position:fixed;
    top: 0;
    left:0;right: 0;bottom: 0;
    display: flex;
    justify-content: center;flex-direction: column;
    align-items: center;
    z-index: 999;
}

.textError{
    text-align: center;
}