:root{

    --bgCard : #F4F4F4;
    --textCard : #212121

    }
    



#detailBook {
    background-color: var(--bgCard);
    position: fixed;
    top: 0;right: 0;left: 0;bottom: 0;
    width: 100vw;height: 100vh;
    z-index: 1000;
    overflow: scroll;
}

.closeIcon {
    position: absolute;top:1rem;left:1.3rem;cursor: pointer;opacity: 0.7;
    display: flex;flex-direction: column;
}

.closeIcon:hover{
    opacity: 1;
}
.textExit{
    transform: translateY(-9px) translateX(20px) rotate(90deg); transform-origin: 0 0;
  
  }
.detailbookBox{
    margin-top: 1rem;
    background-color: var(--bgCard);
    box-shadow: 2px 2px 5px -4px rgba(0,0,0,0.75);
    -webkit-box-shadow: 2px 2px 5px -4px rgba(0,0,0,0.75);
    -moz-box-shadow: 2px 2px 5px -4px rgba(0,0,0,0.75);
    border:solid 1px #eee;
    padding: 2rem;
}

/* ===== Scrollbar CSS ===== */
  /* Firefox */
  #detailBook {
    scrollbar-width: auto;
    scrollbar-color: #a8a8a8 #ffffff;
  }

  /* Chrome, Edge, and Safari */
  #detailBook::-webkit-scrollbar {
    width: 3px;
  }

  #detailBook::-webkit-scrollbar-track {
    background: transparent;
  }

  #detailBook::-webkit-scrollbar-thumb {
    background-color: #a8a8a8;
    border-radius: 4px;
  }

